import { isPresent } from '@whisklabs/typeguards';

import { identify } from 'helpers/analytics/wrapper';
import { initSentry } from 'startup/initSentry';
import { initTracking } from 'startup/initTracking';
import { BackgroundState, RootMessage } from 'types/messages';

import { handleOpenAuth } from './messageHandlers/auth';
import { handleGetUser } from './messageHandlers/user';

initSentry();
initTracking();

let state: BackgroundState = {
  user: {},
};

const handleMessages = async (
  message: RootMessage,
  sender: chrome.runtime.MessageSender,
  // tslint:disable-next-line:no-any
  sendResponse: (response?: any) => void
) => {
  state = await handleGetUser(state, message, sender, sendResponse);
  state = await handleOpenAuth(state, message, sender, sendResponse);

  const userData = state.user.data;
  if (isPresent(userData)) {
    identify(userData.anonymous, userData.id);
  }
};

chrome.runtime.onMessage.addListener((...args) => {
  handleMessages(...args);

  return true;
});
