import { Array, Boolean, Partial, Static, String } from 'runtypes';

import { PhoneBEScheme } from 'api/shared/other';

export const CountryBEScheme = Partial({
  code: String,
  display_name: String,
});

export type CountryBE = Static<typeof CountryBEScheme>;

export const EmailBEScheme = Partial({
  email: String,
  verified: Boolean,
});

export type EmailBE = Static<typeof EmailBEScheme>;

const WhiskIdentityBEScheme = Partial({
  email: EmailBEScheme,
  phone: PhoneBEScheme,
});

const FacebookIdentityBEScheme = Partial({
  email: String,
});

const GoogleIdentityBEScheme = Partial({
  email: String,
});

const SamsungIdentityBEScheme = Partial({
  email: String,
});

const AppleIdentityBEScheme = Partial({
  email: String,
});

export const UserIdentityBEScheme = Partial({
  whisk: WhiskIdentityBEScheme,
  facebook: FacebookIdentityBEScheme,
  google: GoogleIdentityBEScheme,
  samsung: SamsungIdentityBEScheme,
  apple: AppleIdentityBEScheme,
});

export type UserIdentityBE = Static<typeof UserIdentityBEScheme>;

export const UserSettingsBEScheme = Partial({
  language: String,
  country_object: CountryBEScheme,
});

export type UserSettingsBE = Static<typeof UserSettingsBEScheme>;

export const UserBEScheme = Partial({
  id: String,
  identities: Array(UserIdentityBEScheme),
  settings: UserSettingsBEScheme,
});

export type UserBE = Static<typeof UserBEScheme>;
