import { isPresent, isUndefined } from '@whisklabs/typeguards';

import { CountryBE, UserBE, UserSettingsBE } from 'api/user/user';
import { getSupportedLanguage } from 'helpers/languages';
import { Language } from 'types/common';
import { User, UserCountry, UserSettings } from 'types/user';

export const adaptLanguage = (
  language: string | undefined,
  countryCode?: string
): Language | undefined =>
  isPresent(language) ? getSupportedLanguage(language, countryCode) : undefined;

export const adaptCountry = (country: CountryBE = {}): UserCountry =>
  isPresent(country.code) && isPresent(country.display_name)
    ? { code: country.code, displayName: country.display_name }
    : null;

export const adaptUserSettings = (
  settings: UserSettingsBE = {}
): UserSettings => {
  const country = adaptCountry(settings.country_object);

  return {
    language: adaptLanguage(settings.language, country?.code),
  };
};

export const adaptUser = (user: UserBE = {}): User | undefined => {
  if (isUndefined(user.id)) return;

  const settings = adaptUserSettings(user.settings);

  return {
    id: user.id,
    anonymous: (user.identities ?? []).length === 0,
    settings,
  };
};
