import { OPEN_AUTH } from 'constants/messages';
import { openAuthWindow } from 'helpers/tabs';
import { MessageHandler } from 'types/messages';

export const handleOpenAuth: MessageHandler = async (
  state,
  message,
  _sender,
  sendResponse
) => {
  if (message.type !== OPEN_AUTH) return state;

  openAuthWindow();

  sendResponse(null);

  return state;
};
