import { captureException } from '@sentry/browser';
import { isPresent } from '@whisklabs/typeguards';
import { Partial, Static } from 'runtypes';

import { adaptGetMeResponse } from 'api/adapters/user/userApi';

import { baseApi } from '../baseApi';
import { UserBEScheme } from './user';

const GetMeResponseScheme = Partial({
  user: UserBEScheme,
});

export type GetMeResponse = Static<typeof GetMeResponseScheme>;

class UserApi {
  getMe() {
    return baseApi
      .request({
        endpoint: `/v1/me`,
        method: 'GET',
        validate: GetMeResponseScheme,
      })
      .then(adaptGetMeResponse)
      .then(user => {
        if (isPresent(user)) return user;

        const error = new Error('Get user: not valid response');
        captureException(error);

        throw error;
      });
  }
}

export const userApi = new UserApi();
