import { userApi } from 'api/user/userApi';
import { GET_USER } from 'constants/messages';
import { getUserToken } from 'helpers/cookies';
import { GetUserResponse, MessageHandler } from 'types/messages';
import { User } from 'types/user';

enum GetUserStatus {
  UsePrevious,
  NoUser,
}

const getUser = async (
  loadedUser?: User,
  loadedToken?: string
): Promise<{ user: User; token: string } | GetUserStatus> => {
  const token = await getUserToken();

  if (token === undefined) {
    return GetUserStatus.NoUser;
  }

  if (
    loadedToken !== undefined &&
    loadedToken === token &&
    loadedUser !== undefined
  ) {
    return GetUserStatus.UsePrevious;
  }

  try {
    const user = await userApi.getMe();

    return { user, token };
  } catch (e) {
    return GetUserStatus.NoUser;
  }
};

export const handleGetUser: MessageHandler<GetUserResponse> = async (
  state,
  message,
  _sender,
  sendResponse
) => {
  if (message.type !== GET_USER) return state;

  const result = await getUser(state.user.data, state.user.token);

  if (result === GetUserStatus.NoUser) {
    sendResponse(null);

    return state;
  }

  if (result === GetUserStatus.UsePrevious) {
    const user = state.user.data;
    sendResponse(user !== undefined ? user : null);

    const loadedUser = await userApi.getMe();

    return {
      ...state,
      user: {
        ...state.user,
        data: loadedUser,
      },
    };
  }

  sendResponse(result.user);

  return {
    ...state,
    user: {
      data: result.user,
      token: result.token,
    },
  };
};
